import React, { Component } from 'react'
import {Meteor} from 'meteor/meteor';
import {Mongo} from 'meteor/mongo';
import { withTracker } from 'meteor/react-meteor-data';

const Lists = new Mongo.Collection('Lists');

class BlogList extends Component {
	onInsertList = () => {
		Meteor.call('lists.insert', {locale: 'en'}, (err, ret) => {
			console.log(err, ret);
		});
	}

	onDeleteList = (listId) => {
		Meteor.call('lists.remove', {listId}, (err, ret) => {
			console.log(err, ret);
		});
	}

  
  render () {
    const {loading, lists,} = this.props;
    return (
      <div>
      	<button onClick={this.onInsertList}>insert list </button>
	      {
	      	lists.map((list) => {
	      		return (
	      			<div onClick={() => this.onDeleteList(list._id)} key={list._id}>{list.name}</div>
	      		);
	      	})
	      }
      </div>
    )
  }
}


const BlogWithData = withTracker(() => {
  const publicHandle = Meteor.subscribe('lists.public');
  const privateHandle = Meteor.subscribe('lists.private');
  console.log('appcontainer remove console.log')
  return {
    loading: !(publicHandle.ready() && privateHandle.ready()),
    lists: Lists.find({ $or: [
      { userId: { $exists: false } },
      { userId: Meteor.userId() },
    ] }).fetch(),
  };
})(BlogList);

// /////////////////////////////////////////////////////////
// Actual container
const Blog = () => (
  <div>
  	This Is Blog
    <BlogWithData />
  </div>
)

export default Blog



