import React from 'react'
import {Meteor} from 'meteor/meteor';
import { withTracker } from 'meteor/react-meteor-data';

const Person = (props) => {
  const {user, connected,} = props;
  return (
    <div>
      <p>Loaded successfully:</p>
      <p>user: {user && user.toString()}</p>
      <p>connected: {connected.toString()}</p>
    </div>
  )
}


const PersonWithData = withTracker(() => {
  return {
    user: Meteor.user(),
    connected: Meteor.status().connected,
  };
})(Person);

// /////////////////////////////////////////////////////////
// Actual container
const About = () => (
  <div>
    <h1>This is what we're all about.</h1>
    <p>React, static sites, performance, speed. It's the stuff that makes us tick.</p>
    <h2>Here is a person loaded from graphql:</h2>
    <PersonWithData />
  </div>
)

export default About


